<template>
    <div class="curing">
        <div class="title-w">
            <div class="title-w-bg"></div>
            <div class="title-w-body">
                <div class="icon left-icon"></div>
                <div class="title">养护巡检运行情况</div>
                <div class="icon right-icon"></div>
            </div>
        </div>
        <div class="top-w clearfix">
            <div class="left-w fl">
                <div class="title">养护</div>
                <div class="line-w right">
                <div class="line"></div>
                <i class="circle"></i>
                </div>
                <span class="legend">养护人员与在线数</span>
                <div class="chart-w" ref="curingpc"></div>
            </div>
            <div class="right-w fl">
                <div class="header-w">
                    <div class="line-w right"><div class="line"></div><i class="circle"></i></div>
                    <div class="legend"></div>
                    <div class="line-w left"><div class="line"></div><i class="circle"></i></div>
                </div>
                <div class="table-w">
                    <table>
                        <thead>
                            <tr>
                                <template v-for="th in thead">
                                    <th :style="{width: `${thead.length/100}%`}">{{th}}</th>
                                </template>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(row, k) in tbody">
                                <td>{{k+1}}</td>
                                <td>江汉</td>
                                <td>G54</td>
                                <td>10</td>
                                <td>2</td>
                                <td>正常</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div class="bottom-w clearfix">
            <div class="left-w fl">
                <div class="title">巡检</div>
                <div class="line-w right">
                <div class="line"></div>
                <i class="circle"></i>
                </div>
                <span class="legend">巡检人员与在线数</span>
                <div class="chart-w" ref="checkpc"></div>
            </div>
            <div class="right-w fl">
                <div class="header-w">
                    <div class="line-w right"><div class="line"></div><i class="circle"></i></div>
                    <div class="legend"></div>
                    <div class="line-w left"><div class="line"></div><i class="circle"></i></div>
                </div>
                <div class="table-w">
                    <table>
                        <thead>
                            <tr>
                                <template v-for="th in thead">
                                    <th :style="{width: `${thead.length/100}%`}">{{th}}</th>
                                </template>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(row, k) in tbody">
                                <td>{{k+1}}</td>
                                <td>江汉</td>
                                <td>G54</td>
                                <td>10</td>
                                <td>2</td>
                                <td>正常</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import echarts from 'echarts';

    export default {
        name: 'MCuring',
        data () {
            return {
                clgcData: [
                    {name: '江汉', value: 100}, 
                    {name: '鄂西北', value: 320}, 
                    {name: '鄂西', value: 200}, 
                    {name: '保宜',value: 90}, 
                    {name: '鄂东', value: 180}, 
                    {name: '楚天', value: 180}
                ],
                thead: ['序号', '分区', '线路', '人数', '车辆', '出勤状态'],
                tbody: [
                    {},
                    {},
                    {},
                    {},
                    {}
                ]
            }
        },
        methods: {
            // 
            drawPie(chart, data, el){
                if (!chart) {
                chart = echarts.init(el);
                }
                let option = {
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                grid: {
                    left: 10,
                    right: 10,
                    top: 20,
                    bottom: 40
                },
                color: ['#99ffff', '#00FFFF', '#66CCFF', '#3399cc', '#33CCFF', '#CCFFFF'],
                series: [
                    {
                        name: '报警',
                        type: 'pie',
                        radius: '80%',
                        center: ['50%', '50%'],
                        data: data,
                        label: {
                            normal: {
                                position: 'inside',
                                color: '#ffffff',
                                fontSize: 16
                            }
                        },
                        labelLine: {
                            lineStyle: {
                            color: '#00edff'
                            }
                        }
                    },
                    {
                        name: '报警',
                        type: 'pie',
                        radius: '70%',
                        roseType: 'radius',
                        center: ['50%', '50%'],
                        data: data,
                        label: {
                            normal: {
                                show: false,
                                position: 'inside',
                                color: '#00edff',
                                fontSize: 16
                            }
                        },
                        itemStyle: {
                            color: '#0f0',
                            opacity: .2
                        },
                        labelLine: {
                            lineStyle: {
                            color: '#00edff'
                            }
                        }
                    }
                ]
                };
                chart.setOption(option)
            }
        },
        mounted () {
            this.drawPie(echarts.init(this.$refs['curingpc']), this.clgcData, null);
            this.drawPie(echarts.init(this.$refs['checkpc']), this.clgcData, null);
        }
    }
</script>
<style>
</style>


