<template>
    <div class="platform">
        <div class="title-w">
            <div class="title-w-bg"></div>
            <div class="title-w-body">
                <div class="icon left-icon"></div>
                <div class="title">平台运行情况</div>
                <div class="icon right-icon"></div>
            </div>
        </div>
        <div class="run-status-w sub-title-w">
            <p class="title">传输状态</p>
            <ul class="charts-w clearfix">
                <li class="fl">
                    <div class="chart-w" ref="build-l"></div>
                    <p class="legend">施工监控</p>
                </li>
                <li class="fl">
                    <div class="chart-w" ref="curing-l"></div>
                    <p class="legend">巡检养护</p>
                </li>
                <li class="fl">
                    <div class="chart-w" ref="env-l"></div>
                    <p class="legend">环境监测</p>
                </li>
                <li class="fl">
                    <div class="chart-w" ref="bridge-l"></div>
                    <p class="legend">桥梁健康监测</p>
                </li>
            </ul>
        </div>
        <div class="server-status-w sub-title-w">
            <p class="title">服务器状态</p>
            <ul class="server-w clearfix">
                <li v-for="(item, i) in serverData" :key="`server-item-${i}`" class="fl">
                    <div class="inner clearfix">
                        <div class="left-b fl">
                            <i class="icon"></i>
                            <p class="name">{{item.name}}</p>
                        </div>
                        <div class="right-b fl">
                            <div class="prop-w">
                                <p class="label">CPU占用率</p>
                                <div class="progress-w">
                                    <div class="progress" :style="{width: `${item.cupP}%`}"></div>
                                    <span class="text">{{item.cupP}}%</span>
                                </div>
                            </div>
                            <div class="prop-w">
                                <p class="label">DISK使用率</p>
                                <div class="progress-w">
                                    <div class="progress" :style="{width: `${item.diskP}%`}"></div>
                                    <span class="text">{{item.diskP}}%</span>
                                </div>
                            </div>
                            <div class="prop-w">
                                <p class="label">内存使用率</p>
                                <div class="progress-w">
                                    <div class="progress" :style="{width: `${item.memP}%`}"></div>
                                    <span class="text">{{item.memP}}%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    import echarts from 'echarts';

    export default {
        name: 'MPlatform',
        data () {
            return {
                runStatusData: [
                    {time: '1', thisYear: 340}, 
                    {time: '2', thisYear: 410}, 
                    {time: '3', thisYear: 420}, 
                    {time: '4', thisYear: 360}, 
                    {time: '5', thisYear: 475}, 
                    {time: '6', thisYear: 480}, 
                    {time: '7', thisYear: 516}, 
                    {time: '8', thisYear: 580}, 
                    {time: '9', thisYear: 550}, 
                    {time: '10', thisYear: 580}, 
                    {time: '11', thisYear: 540}, 
                    {time: '12',thisYear: 480}
                ],
                serverData: [
                    {
                        name: '服务器1',
                        cupP: '20',
                        diskP: '40',
                        memP: '20'
                    },
                    {
                        name: '服务器2',
                        cupP: '20',
                        diskP: '40',
                        memP: '20'
                    },
                    {
                        name: '服务器3',
                        cupP: '20',
                        diskP: '40',
                        memP: '20'
                    },
                    {
                        name: '服务器4',
                        cupP: '20',
                        diskP: '40',
                        memP: '20'
                    },
                    {
                        name: '服务器5',
                        cupP: '20',
                        diskP: '40',
                        memP: '20'
                    },
                    {
                        name: '服务器6',
                        cupP: '20',
                        diskP: '40',
                        memP: '20'
                    }
                ]
            }
        },
        methods: {
            // modal line 图Inverst
            drawMLStatus (chart, data, el) {
                let xData = [], yData = [], min = null, max = null;

                for (let i = 0; i < data.length; i++) {
                xData.push(data[i].time);
                yData.push(data[i].thisYear);
                if (min === null) {
                    min = data[i].thisYear;
                } else {
                    min = Math.min(min, data[i].thisYear);
                }
                if (max === null) {
                    max = data[i].thisYear;
                } else {
                    max = Math.max(max, data[i].thisYear);
                }
                }
                let option = {
                tooltip: {
                    trigger: 'axis',
                    formatter (params) {
                    return '<div style="background: #00edff;color: #fff; line-height: 1;padding: 1px 5px">' + params[0].value + '</div>';
                    },
                    padding: 0,
                    axisPointer: {
                    lineStyle: {
                        width: 8,
                        color: '#00edff'
                    }
                    }
                },
                grid: {
                    left: 50,
                    right: 28,
                    top: 30,
                    bottom: 20,
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    axisLabel: {
                    color: '#00edff'
                    },
                    axisLine: {
                    show: false
                    },
                    axisTick: {
                    show: false
                    },
                    splitLine: {
                    show: false,
                    lineStyle: {
                        color: "#122b3f"
                    }
                    },
                    data: xData
                },
                yAxis: {
                    name: '',
                    type: 'value',
                    min: min,
                    max: max,
                    axisLabel: {
                    color: '#00edff'
                    },
                    axisLine: {
                    show: false
                    },
                    axisTick: {
                    show: false
                    },
                    splitLine: {
                    show: false,
                    lineStyle: {
                        color: "#122b3f"
                    }
                    },
                    nameTextStyle: {
                    color: '#00edff',
                    align: 'left'
                    }
                },
                series: [
                    {
                    name: '养护资金投入',
                    type: 'line',
                    smooth: false,
                    symbol: 'circle',
                    symbolSize: 6,
                    itemStyle: {
                        normal: {
                        color: 'rgb(0, 237, 255)'
                        }
                    },
                    areaStyle: {
                        normal: {
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                            offset: 0,
                            color: 'rgba(0, 237, 255,1)'
                        }, {
                            offset: 1,
                            color: 'rgba(0, 237, 255,0)'
                        }])
                        }
                    },
                    data: yData
                    }
                ]
                };
                chart.setOption(option);
            },
        },
        mounted () {
            this.drawMLStatus(echarts.init(this.$refs['build-l']), this.runStatusData, null);
            this.drawMLStatus(echarts.init(this.$refs['curing-l']), this.runStatusData, null);
            this.drawMLStatus(echarts.init(this.$refs['env-l']), this.runStatusData, null);
            this.drawMLStatus(echarts.init(this.$refs['bridge-l']), this.runStatusData, null);
        }
    }
</script>
<style>
</style>


