<template>
    <div class="env">
        <div class="title-w">
            <div class="title-w-bg"></div>
            <div class="title-w-body">
                <div class="menu left-menu">
                    <a :class="{active: menuActive == 'menu-1'}" @click.stop="menuClickEvent('menu-1')">大气</a>
                    <a :class="{active: menuActive == 'menu-2'}" @click.stop="menuClickEvent('menu-2')">噪音</a>
                    <a :class="{active: menuActive == 'menu-3'}" @click.stop="menuClickEvent('menu-3')">水污染</a>
                </div>
                <div class="icon left-icon"></div>
                <div class="title">环境监测情况</div>
                <div class="icon right-icon"></div>
            </div>
        </div>
        <ul class="content-w">
            <li v-if="menuActive == 'menu-1'">
                <div class="charts-w clearfix">
                    <div class="left-w fl">
                        <span class="legend">报警统计</span>
                        <div class="chart-w-warn" ref="warnpc"></div>
                    </div>
                    <div class="center-w fl">
                        <div class="header-w">
                            <div class="legend">处理率</div>
                        </div>
                        <ul class="handle-w clearfix">
                            <li class="fl" v-for="(item, i) in handleData">
                                <span class="label">{{item.name}}</span>
                                <div class="progress-w">
                                    <div class="progress" :style="{width: `${item.value}%`}"></div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div class="right-w fl">
                        <div class="header-w"><span class="legend">噪音指数</span></div>
                        <div class="chart-w-mqi" ref="mqilc"></div>
                    </div>

                </div>
            </li>
            <li v-else-if="menuActive == 'menu-2'"></li>
            <li v-else></li>
        </ul>
    </div>
</template>
<script>
    import echarts from 'echarts';

    export default {
        name: 'MEnv',
        data () {
            return {
                menuActive: 'menu-1',
                clgcData: [
                    {name: '江汉', value: 100}, 
                    {name: '鄂西北', value: 320}, 
                    {name: '鄂西', value: 200}, 
                    {name: '保宜',value: 90}, 
                    {name: '鄂东', value: 180}, 
                    {name: '楚天', value: 180}
                ],
                handleData: [
                    {name: '江汉', value: 80}, 
                    {name: '鄂西北', value: 60}, 
                    {name: '鄂西', value: 50}, 
                    {name: '保宜',value: 40}, 
                    {name: '鄂东', value: 30}, 
                    {name: '楚天', value: 60}
                ],
            }
        },
        methods: {
            // 菜单点击
            menuClickEvent (menu) {
                if (menu != this.menuActive) {
                    this.menuActive = menu;
                }
            },
            // pie
            drawPie(chart, data, el){
                if (!chart) {
                chart = echarts.init(el);
                }
                let option = {
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                grid: {
                    left: 10,
                    right: 10,
                    top: 20,
                    bottom: 40
                },
                color: ['#99ffff', '#00FFFF', '#66CCFF', '#3399cc', '#33CCFF', '#CCFFFF'],
                series: [
                    {
                    name: '报警',
                    type: 'pie',
                    radius: '80%',
                    center: ['50%', '50%'],
                    data: data,
                    label: {
                        normal: {
                            position: 'inside',
                            color: '#00edff',
                            fontSize: 16
                        }
                    },
                    labelLine: {
                        lineStyle: {
                        color: '#00edff'
                        }
                    }
                    }
                ]
                };
                chart.setOption(option)
            },
            // MQI画图 line
            drawMQI (chart, data, el) {
                let options;

                options = {
                color: '#00edff',
                tooltip: {
                    trigger: 'axis'
                },
                grid: {
                    containLabel: true,
                    top: 20,
                    left: 0,
                    right: 0,
                    bottom: 20
                },
                xAxis: {
                    type: 'category',
                    splitLine: {
                    show: true,
                    lineStyle: {
                        color: 'rgba(255,255,255, .1)'
                    },
                    },
                    axisTick: {show: false},
                    axisLine: {show: false},
                    axisLabel: {
                    color: '#00edff'
                    },
                    data: [2011, 2012, 2013, 2014, 2015, 2016, 2017]
                },
                yAxis: {
                    type: 'value',
                    min: 50,
                    max: 100,
                    interval: 10,
                    splitLine: {show: false},
                    axisTick: {show: false},
                    axisLine: {show: false},
                    axisLabel: {
                    color: '#00edff'
                    },
                    splitArea: {
                    show: true,
                    areaStyle: {
                        color: ['rgba(204, 0, 51, .3)', 'rgba(204, 51, 51, .3)', 'rgba(255, 215, 0, .3)', 'rgba(51, 204, 51, .3)', 'rgba(51, 255, 102, .3)']
                    }
                    }
                },
                series: [
                    {
                    type: 'line',
                    data: data
                    }
                ]
                }
                chart.setOption(options);
            }
        },
        mounted () {
            this.drawPie(echarts.init(this.$refs['warnpc']), this.clgcData, null);
            this.drawMQI(echarts.init(this.$refs['mqilc']), [90, 70.5, 70, 80, 90, 90.2, 90.5], null);
        }
    }
</script>
<style>
</style>


