<template>
    <div :class="`modal-w ${size} ${slider}`">
        <div class="bg"></div>
        <div class="guide-line"></div>
        <div class="close-icon" @click.stop="closeEvent"></div>
        <div class="body">
            <slot></slot>
        </div>
    </div>
</template>
<script>
    // 模态框插件
    export default {
        props: {
            size: {
                type: String,
                default: 'one' // one/two/three
            },
            slider: {
                type: String,
                default: 'bottom' // left/bottom
            }
        },
        data () {
            return {}
        },
        methods: {
            // 关闭按钮
            closeEvent () {
                if (!/(^|\s)hide(\s|$)/.test(this.$el.className)) {
                    this.$el.className += ' hide';
                    this.$emit('mCloseEvent');
                }
            }
        },
        mounted () {
            let vm = this;

            setTimeout(() => {
                vm.$el.className += ' in';
            }, 100);
        }
    }
</script>
 <style lang="scss" scoped>
    // 模态框
    @import '../scss/variables';
    .modal-w {
        &.one {
            width: 1060px;
            height: 750px;
            margin-left: -530px;
            .bg {
                background-image: url("../assets/modal-bg-1.png");
            }
            .guide-line {
                display: block;
            }
        }
        &.two {
            width: 1356px;
            height: 750px;
            margin-left: -678px;
            .bg {
                background-image: url("../assets/modal-bg-2.png");
            }
        }
        &.three {
            width: 1060px;
            height: 710px;
            margin-left: -530px;
            .bg {
                background-image: url("../assets/modal-bg-3.png");
            }
        }
        &.left {
            top: 120px;
            left: -120%;
        }
        &.bottom {
            top: 120%;
            left: 50%;
        }
        &.in {
            top: 190px;
            left: 50%;
        }
        &.hide {
            top: 120%!important;
        }
        position: fixed;
        top: 190px;
        z-index: 9999;
        font-weight: 200;
        color: $mainColor;
        transition: all .5s ease-out;        
        .bg {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 10000;
            background: transparent center/contain no-repeat;
        }
        .guide-line {
            display: none;
            position: absolute;
            top: -75px;
            left: 0;
            width: 60%;
            height: 80px;
            background: transparent url("../assets/guide-line1.png") left center/contain no-repeat;
        }
        .close-icon {
            position: absolute;
            top: -80px;
            right: 45px;
            display: block;
            width: 54px;
            height: 80px;
            background: transparent url("../assets/close-icon.png") center/contain no-repeat;
            cursor: pointer;
        }
        .body {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 10001;
            padding: 0 30px;
        }
    }
 </style>


