</template>
<script>
    import echarts from 'echarts';

    export default {
        name: 'MRoadnet',
        data () {
            return {
                menuActive: 'menu-1',
                clgcData: [
                    {name: '江汉', value: 100}, 
                    {name: '鄂西北', value: 320}, 
                    {name: '鄂西', value: 200}, 
                    {name: '保宜',value: 90}, 
                    {name: '鄂东', value: 180}, 
                    {name: '楚天', value: 180}
                ],
                handleData: [
                    {name: '江汉', value: 80}, 
                    {name: '鄂西北', value: 60}, 
                    {name: '鄂西', value: 50}, 
                    {name: '保宜',value: 40}, 
                    {name: '鄂东', value: 30}, 
                    {name: '楚天', value: 60}
                ],
                thead1: ['序号', '所属路线', '桩号', '高度', '结构方式', '防护方式', '报警类型', '报警级别', '报警时间', '处理状态', '处理时间', '操作'],
                tbody1: [
                    {
                    },
                    {},
                    {},
                    {},
                    {}                    
                ]
                

            }
        },
        methods: {
            // 菜单点击
            menuClickEvent (menu) {
                if (menu != this.menuActive) {
                    this.menuActive = menu;
                }
            },
            // 
            drawPie(chart, data, el){
                if (!chart) {
                chart = echarts.init(el);
                }
                let option = {
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                grid: {
                    left: 10,
                    right: 10,
                    top: 20,
                    bottom: 40
                },
                color: ['#99ffff', '#00FFFF', '#66CCFF', '#3399cc', '#33CCFF', '#CCFFFF'],
                series: [
                    {
                    name: '报警',
                    type: 'pie',
                    radius: '80%',
                    center: ['50%', '50%'],
                    data: data,
                    label: {
                        normal: {
                            position: 'inside',
                            color: '#00edff',
                            fontSize: 16
                        }
                    },
                    labelLine: {
                        lineStyle: {
                        color: '#00edff'
                        }
                    }
                    }
                ]
                };
                chart.setOption(option)
            }
        },
        mounted() {
            this.drawPie(echarts.init(this.$refs['warnpc']), this.clgcData, null);
        },
    }
</script>
<style>
