<template>
  <div :class="`rotate-box ${direction} ${animateType}`">
    <div class="rotate-con" :class="direction === 'left'?'rotate-left':'rotate-right'" :style="{transform: 'rotateY('+(direction === 'left'?-deg:deg)+'deg)'}">
      <div class="title-box rotate-title-box" @click.stop="bodyAnimateEVent">
        <div class="inner">
          <i class="cb cb-left"></i>
          <p class="title-info">{{title}}</p>
          <i class="cb cb-right"></i>
        </div>
      </div>
      <div class="rotate-body-box">
        <el-scrollbar class="scrollbar" wrap-class="scrollwrap">
            <slot></slot>
        </el-scrollbar>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      direction: {
        type: String,
        default: 'right'
      },
      deg: {
        type: Number,
        default: 0
      },
      title: {
        type: String,
        default: '标题'
      },
      animateType: {
        type: String,
        default: 'rotate'
      },
      expanded: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      // 侧标栏显示隐藏动画
      bodyAnimateEVent () {
        let boxs = document.querySelectorAll('.rotate-box');

        if (!this.expanded) {
          return;
        }
        boxs = [].slice.call(boxs);
        boxs.map((box) => {
          if (/(^|\s)hide(\s|$)/.test(box.className)) {
            box.className = box.className.replace(/(^|\s)hide(\s|$)/, '');
          }
          else {
            box.className += ' hide';
          }
        });
      }
    },
    mounted () {
      let vm = this;
      setTimeout(() => {
        vm.$el.className = vm.$el.className.replace(/(^|\s)(rotate|slider)(\s|$)/, '');
      }, 500);
    }
  }
</script>
<style scoped>
  .rotate-box {
    width: 450px;
    height: 100%;
    perspective: 800px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    transition: transform .3s ease-out;
  }
  .rotate-box.rotate {
    transform: rotateY(90deg);
  }
  .rotate-box.left.slider {
    transform: translateX(100%);
  }
  .rotate-box.right.slider {
    transform: translateX(-100%);
  }
  .left-con {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2000;
  }
  .right-con {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 2000;
  }
  .rotate-con {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
  }
  .rotate-right {
    transform-origin: left center;
    -webkit-transform-origin: left center;
    -moz-transform-origin: left center;
  }
  .rotate-left {
    transform-origin: right center;
    -webkit-transform-origin: right center;
    -moz-transform-origin: right center;
  }
  .rotate-title-box {
    /* visibility: hidden; */
  }
  .title-box {
    width: 100%;
    padding: 15px 0;
    cursor: pointer;
    background-color: rgba(16, 36, 50, 0.9);
  }
  .title-info {
    display: inline-block;
    vertical-align: middle;
    width: 41%;
    margin: 0;
    text-align: center;
    font-size: 22px;
    color: white;
  }
  .title-box .inner {
    position: relative;
    width: 95%;
    height: 50px;
    margin: auto;
    background-size: 100% 100%;
    background-image: url("../assets/title-con.png");
  }
  .cb {
    display: inline-block;
    vertical-align: middle;
    width: 28%;
    height: 100%;
    background-size: 95% auto;
    background-position: center 55%;
    background-repeat: no-repeat;
  }
  .cb-left {
    background-image: url("../assets/cb-left.png");
  }

  .cb-right {
    background-image: url("../assets/cb-right.png");
  }
  .rotate-body-box {
    height: calc(100% - 80px);
    padding: 0 15px 15px;
    background-color: rgba(16, 36, 50, 0.9);
    transition: transform .3s ease-out;    
  }
  .rotate-box.hide .rotate-body-box {
    transform: rotateY(90deg);
  }
  .scrollbar {
    height: 100%;
  }
</style>



