import axios from 'axios';

export default {
    install (Vue, options) {
        /**
         * @desc axios修改
         */
        // dev: 172.20.104.184:9084
        // prod: http://dev.sibat.cn
        axios.defaults.baseURL = 'http://172.20.104.184:9084';
        axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
        axios.defaults.timeout = 10000;
        // Add a request interceptor
        axios.interceptors.request.use(function (config) {
            // Do something before request is sent
            return config;
        }, function (error) {
            // Do something with request error
            return Promise.reject(error);
        });
 
        // Add a response interceptor
        axios.interceptors.response.use(function (response) {
            // Do something with response data
            let data = response.data;
            if (data.status != 200) {
                console.error('数据有误!');
            }
            return response.data;
        }, function (error) {
            // Do something with response error
            console.error('交互失败!');
            return Promise.reject(error);
        });
        Vue.prototype.$axios = axios;

        /**
         * @desc 小数点后末尾加0
         * @param {Number/String} num 数字
         * @param {Number} bit 位数
         * @return {String} 补全零的字符串
         */
        Vue.prototype.$pointEndAdd0 = (num, bit) => {
            if (!num || !bit) {
                console.error('请输入num和bit');
                return;
            }
            num = Math.round(num*Math.pow(10, bit))/Math.pow(10, bit);

            let addZero = () => {
                let reg = new RegExp('\\.\\d{' + bit + '}$', '');

                if (reg.test(num)) {
                    return num;
                }
                if (!/\./.test(num)) {
                    num += '.';
                }
                num += '0';
                return addZero(num, bit);
            };
            return addZero();
        }

        /**
         * @desc 数字1000逗号分割
         * @param {Number/String} num 数字
         * @return {String} 分割后的数据
         */
        Vue.prototype.$thousandCut = (num) => {
            let splits = [];
            num = num.toString();
            for (let i = num.length; i >= 1; i -= 3) {
                splits.push(num.substring(i-3>=0? i-3: 0, i));
            }
            splits.reverse();
            return splits.join(',');
        }
    }
}


