<template>
  <div class="menu">
    <div class="main-con">
      <div class="menu-left">
        <div class="nav-con">
          <a :class="`menu-a${$route.name == 'AssetsManager'? ' active': ''}`" href="#/assets-m">资产管理</a>
          <a class="menu-a">智能诊断</a>
        </div>
        <p class="time">{{time}}</p>
      </div>
      <div class="menu-center" @click.stop="logoClickEvent">
        <div class="logo"></div>
        <p class="sys-name">高速公路智慧路网运行监测平台</p>
        <div class="head-line"></div>
      </div>
      <div class="menu-right">
        <div class="nav-con">
          <a :class="`menu-a${$route.name == 'Monitor'? ' active': ''}`">
            运行监测
            <ul class="sub-menu">
              <li class="sub-menu-li" v-for="(item, i) in monitor.subMenu" :key="`msub-menu-item-${i}`">
                <a class="sub-menu-a" :href="`#/monitor/${item.value}`">{{item.label}}</a>
              </li>
            </ul>
          </a>
          <a class="menu-a">运营管理</a>
        </div>
        <div class="user-con">
          <span class="username">{{username}}</span>
          <i class="icon config"></i>
          <i class="icon camera"></i>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        time: '2018-1-1 11:33:00',
        username: 'admin',
        timeInterval: 0,
        monitor: {
          subMenu: [
            {label: '平台运行情况', value: 'platform'},
            {label: '路网运行情况', value: 'roadnet'},
            {label: '养护巡查运行情况', value: 'curing'},
            {label: '环境监测情况', value: 'env'},
            {label: '经济运行情况', value: 'profit'},
            {label: '在建项目', value: 'building'}
          ]
        }
      }
    },
    methods:{
      // logo 点击
      logoClickEvent () {
        let mapIframe = document.querySelector('#map-iframe');

        mapIframe.contentWindow.initMapRoad && mapIframe.contentWindow.initMapRoad();
        // 过度页面 refresh 刷新 不要重新加载地图,优化加载速度
        if (this.$route.name == 'Home') {
          this.$router.push({path: '/refresh'});
        }
        setTimeout(() => {
          this.$router.push({path: '/'});
        }, 20);
        
      },
      updateTime(){
          let date = new Date();
          this.time = date.getFullYear()+'-'+(date.getMonth()+1)+'-'+date.getDate()
            +' '+(date.getHours()<10?'0'+date.getHours():date.getHours())+':'
            +(date.getMinutes()<10?'0'+date.getMinutes():date.getMinutes())+':'
            +(date.getSeconds()<10?'0'+date.getSeconds():date.getSeconds());
      }
    },
    mounted(){
      this.timeInterval = setInterval(()=>{this.updateTime()},1000)
    },
    destroy(){
        if(this.timeInterval){
            clearInterval(this.timeInterval);
            this.timeInterval = 0
        }
    }
  }
</script>
<style scoped>
  .menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
    padding-top: 20px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
    background: -o-linear-gradient(top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
    background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }

  .logo {
    display: block;
    width: 120px;
    height: 26px;
    margin: auto;
    background-size: 100% 100%;
    background-image: url("../assets/logo.png");
    background-repeat: no-repeat;
  }

  .main-con {
    width: 1000px;
    height: 95px;
    // overflow: hidden;
    margin: auto;
  }

  .menu-left {
    display: inline-block;
    vertical-align: bottom;
    width: 250px;
    height: 100%;
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: top;
    background-image: url("../assets/menu-left.png");
    margin-right: -30px;
  }

  .menu-right {
    display: inline-block;
    vertical-align: bottom;
    width: 250px;
    height: 100%;
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: top;
    background-image: url("../assets/menu-right.png");
    margin-left: -30px;
  }

  .menu-center {
    display: inline-block;
    vertical-align: top;
    width: 550px;
    cursor: pointer;
  }

  .sys-name {
    margin: 10px 0 0;
    width: 100%;
    text-align: center;
    color: #00edff;
    font-size: 31px;
  }

  .head-line {
    width: 100%;
    height: 46px;
    background-size: 100% 100%;
    background-image: url("../assets/head-line.png");
    background-repeat: no-repeat;
    margin-top: -10px;
  }

  .nav-con {
    padding: 10px 10px;
  }

  .menu-a {
    position: relative;
    cursor: pointer;
    text-decoration: none;
    color: #00aab7;
    font-size: 13px;
    padding-bottom: 5px;
    border-bottom: 1px solid transparent;
  }
  .menu-a:hover,
  .menu-a.active {
    border-color: #00aab7;
  }
  .menu-a:not(:last-child) {
    margin-right: 15px;
  }
  .menu-a:hover .sub-menu {
    border-width: 1px;
    height: 240px;

  }
  .sub-menu {
    position: absolute;
    top: 30px;
    left: 50%;
    width: 200px;
    margin-left: -100px;
    text-align: center;
    background-color: rgba(2, 236, 253, .2);
    border-radius: 4px;
    border: 0 solid rgba(2, 236, 253, .5);
    height: 0;
    overflow: hidden;
    transition: all .3s ease-out;
  }
  .sub-menu-li {
    padding: 0 10px;
  }
  .sub-menu-li:hover {
    background: rgba(255,255,255, .2);
  }
  .sub-menu-li + .sub-menu-li .sub-menu-a {
    border-top: 1px solid rgba(2, 236, 253, .5);
  }
  .sub-menu-a {
    display: block;
    text-decoration: none;
    font-size: 18px;
    height: 40px;
    line-height: 40px;
    color: #fff;
  }
  .time {
    color: #00aab7;
    padding-top: 20px;
    padding-left: 20px;
    font-size: 13px;
  }

  .menu-right > .nav-con {
    padding: 10px 0 10px 100px;;
  }

  .user-con {
    float: right;
    margin-top: 15px;
    padding-right: 20px;
  }
  .username{
    display: inline-block;
    vertical-align: middle;
    color: #00aab7;
    font-size: 13px;
  }
  .icon{
    display: inline-block;
    vertical-align: middle;
    width: 18px;
    height: 18px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin-left: 15px;
    cursor: pointer;
  }
  .config{
    background-image: url("../assets/config.png");
  }
  .camera{
    width: 14px;
    background-image: url("../assets/camera.png");
  }
</style>



