          yData2.push(data[i].lastYear);
          if (min === null) {
            min = Math.min(data[i].thisYear, data[i].lastYear);
          } else {
            min = Math.min(min, data[i].thisYear, data[i].lastYear);
          }
          if (max === null) {
            max = Math.max(data[i].thisYear, data[i].lastYear);
          } else {
            max = Math.max(max, data[i].thisYear, data[i].lastYear);
