      /* 数据加载 */
      /** 
       *@desc 养护区数据
       *@param {String} area_code 养护区代码
       */
      loadMaintenanceData (area_code) {
        let vm = this;

        vm.$axios.get('/hbjt_highway/api/v1/statistics/conservation_area?area_code=' + area_code)
          .then((json) => {
            let data = json.data || {};

            vm.rotateView = 'road';
            vm.$nextTick(() => {
              vm.mtData.roadLen = vm.$pointEndAdd0(data.lineLength || 1030.23, 2); 
              vm.mtData.bridgeLen = vm.$pointEndAdd0(data.bridgeLength || 50.23, 2);
              vm.mtData.bridgeNum = data.bridgeNum || 12;
              vm.mtData.tunnelLen = vm.$pointEndAdd0(data.tunnelLength || 20.56, 2);
              vm.mtData.tunnelNum = data.tunnelNum || 9;
              vm.mtData.serviceNum = data.serviceAreaNum || 12;
              vm.mtData.tollNum = data.tollNum || 10;
              vm.mtData.bridgePercent = (vm.mtData.bridgeLen/vm.mtData.roadLen)*100 + '%';
              vm.mtData.tunnelPercent = (vm.mtData.tunnelLen/vm.mtData.roadLen)*100 + '%';
              vm.drawProfitChart(this.fwqChart, this.fwqProfit, this.$refs.fwq);
              vm.drawProfitChart(this.sfzChart, this.sfzProfit, this.$refs.sfz);
              vm.drawInvestChart(this.yhzjChart, this.yhzjInvest, this.$refs.yhzj);
              vm.drawPie(this.clgcChart, this.clgcData, this.$refs.clgc);
            });
          });
      },
      /** 
       *@desc 路线统计数据
       *@param {String} section_code 路线代码
       */
      loadRoadLineData (section_code) {
        let vm = this;

        vm.$axios.get('/hbjt_highway/api/v1/statistics/section_info?section_code=' + section_code)
        .then((json) => {
          vm.rotateView = 'road-line';
          vm.$nextTick(() => {
            vm.drawProfitChart(this.fwqChart, this.fwqProfit, this.$refs.fwq);
            vm.drawProfitChart(this.sfzChart, this.sfzProfit, this.$refs.sfz);
            vm.drawInvestChart(this.yhzjChart, this.yhzjInvest, this.$refs.yhzj);
            vm.drawPie(this.clgcChart, this.clgcData, this.$refs.clgc);
          });
        });
      },
