       *@desc 路段统计数据
       *@param {String} section_code 路段代码
       */ 
      loadRoadUDData (section_code) {
        let vm = this;

        vm.$axios.get('/hbjt_highway/api/v1/section/section_index?section_code=' + section_code)
        .then((json) => {
          vm.rotateView = 'road-ud';
          vm.$nextTick(() => {
            this.drawMQI(echarts.init(this.$refs['mqi-up']), [90, 70.5, 70, 80, 90, 90.2, 90.5], null);
