              this.drawMLInverst(echarts.init(this.$refs['roadmlc']), this.yhzjInvest, null);
            });
          });
      },
      /** 
       *@desc 点击构造物弹窗数据
       */
      loadRoadPointData (id, type) {
        let vm = this;

        switch (type) {
          case 'serviceArea': 
            vm.modalView = 'service';
            vm.$nextTick(() => {
              this.drawMDLProfitN(echarts.init(this.$refs['servicemlc']), this.sfzProfit, null);
            });
            break;
          case 'toll': 
            vm.modalView = 'tollgate';
            vm.$nextTick(() => {
              this.drawLevel(echarts.init(this.$refs['tollgatempc']), this.tollgatempcData, null);
              this.drawMDLProfitN(echarts.init(this.$refs['tollgatemlc']), this.sfzProfit, null);
            });
            break;
          case 'bridge': 
            vm.modalView = 'bridge';
            vm.$nextTick(() => {
              this.drawMQI(echarts.init(this.$refs['bridgelc']), [90, 70.5, 70, 80, 90, 90.2, 90.5], null);
            });
            break;
          case 'culvert': 
            vm.modalView = 'tunnel';
            vm.$nextTick(() => {
              this.drawMQI(echarts.init(this.$refs['tunnellc']), [90, 70.5, 70, 80, 90, 90.2, 90.5], null);
            });
            break;
          case 'tunnel': 
            break;
        }
      }
    },
    beforeCreate () {
      let vm = this;
      // 地图点击事件路线
      window.roadclickEvent = (ev, status, type) => {
        var id = ev.target.B.extData.pid;
        // console.log(ev, status, pointType);
        switch (status) {
          case 'sectionclick': 
            vm.roadId = id;
            if (type== 0) {
              vm.loadRoadLineData(id);
            }
            else {
              vm.loadRoadUDData(id);
            }
            break;
          case 'pointclick':
            vm.loadRoadPointData(id, type);
            break;
        }

      }
      window.mapClickEvent = (roadClick) => {
        // console.log(roadClick);
        if (roadClick == 2) {
          vm.loadRoadLineData(vm.roadId);
        }
        else if (roadClick == 1) {
          vm.city = 'all';
          vm.loadMaintenanceData(vm.city);
        }
        else {
          console.log();
        }
      }
