  }

  .box-progress {
    display: block;
    height: 5px;
    width: 100%;
    background-color: #007272;
    margin-top: 10px;
  }

  .box-progress-percent {
    height: 100%;
    background-color: #00edff;
  }

  .box-chart {
    width: 100%;
    height: 180px;
  }

  .box-chart-legend {
    position: absolute;
    right: 0;
    margin-top: -20px;
  }

  .box-chart-legend > span {
    color: #00edff;
    padding-right: 20px;
  }
  .pie-name{
    position: absolute;
    text-align: center;
    color: white;
    font-size: 16px;
    margin-top: -30px;
  }
  .box-list{
    width: 100%;
    height: 315px;
    background-size: 100% 100%;
    background-image: url("../assets/list-box.png");
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    padding: 10px 15px;
    position: relative;
  }
  .box-list-title{
    position: absolute;
    color: white;
    font-size: 19px;
    text-align: center;
    left: 0;
    right: 0;
    margin: 10px 0 0;
  }
  .list-count{
    display: inline-block;
    vertical-align: middle;
    font-size: 36px;
    font-family: Digital;
    color: #ffff00;
    width: 90px;
    text-align: center;
  }
  .box-list-con{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    padding-top: 50px;
  }
  .box-list-ul{
    margin: 0;
    padding: 0 40px;
  }
  .box-list-ul > li{
    font-size: 16px;
    padding-bottom: 10px;
  }
  .box-list-icon{
    display: inline-block;
    vertical-align: middle;
    width: 29px;
    height: 25px;
    background-size: 100% 100%;
  }
  .icon-warm{
    background-image: url("../assets/warn.png");
  }
  .icon-danger{
    background-image: url("../assets/danger.png");
  }
  .box-list-no{
    display: inline-block;
    vertical-align: middle;
    width: 120px;
    text-align: center;
    color: #00c9d8;
  }
  .info-warm{
    display: inline-block;
    vertical-align: middle;
    color: #00c9d8;
  }
  .info-danger{
    display: inline-block;
    vertical-align: middle;
    color: #ea5353;
  }
</style>
<style>
  .scrollwrap {
    overflow-x: hidden !important;
  }
</style>



